<template>
    <div class="filter">
        <select class="filter__select" @change="selectFilter" v-model="selected">
            <option v-for="(option, i) of selectOptions" :value="i" :key="i">{{ option }}</option>
            <option value="reset" v-if="reset">----</option>
        </select>
        <div class="filter__pills">
            <div class="filter__pill" v-for="(filter, i) of selectedFilters" :key="i" @click="removeFilter(filter)">
                <span class="filter__pill-text">{{ filter }}</span>
                <span class="filter__pill-remove">X</span>
            </div>
        </div>
    </div>
</template>

<script>
  export default {
    name: "SjFilters",
    props: {
    },
    data() {
      return {
        selected: 0,
        reset: false,
        updateFiltersEvent: 'updateFilters',
        selectedFilters: [],
        selectOptions: [
            'All',
            'Registered',
            'In Progress',
            'Completed',
        ],
      };
    },
    methods: {
      removeFilter(filter) {
        const index = this.selectedFilters.indexOf(filter);
        this.selectedFilters.splice(index, 1);

        this.reset = true;
        this.selected = 'reset';

        this.$emit(this.updateFiltersEvent, this.selectedFilters);
      },
      selectFilter(event) {
        this.reset = false;
        const newFilter = this.selectOptions[event.target.value];

        if (!newFilter) {
          return;
        }

        if (newFilter === this.selectOptions[0]) {
          this.selectedFilters = [];
          this.$emit(this.updateFiltersEvent, this.selectedFilters);
        } else if (!this.selectedFilters.includes(newFilter)) {
          this.selectedFilters.push(newFilter);
          this.$emit(this.updateFiltersEvent, this.selectedFilters);
        }
      },
    },
  }
</script>

<style scoped lang="scss">
.filter {
    display: flex;
    justify-self: flex-start;
    height: 30px;
    align-items: center;
    &__select {
        font-size: 14px;
    }
    &__pills {
        display: inline-flex;
    }
    &__pill {
        user-select: none;
        padding: 2px 10px;
        margin-left: 10px;
        border: 1px solid hotpink;
        border-radius: 20px;
        &:hover {
            cursor: pointer;
            background: #fffafc;

            .filter__pill-remove {
                color: hotpink;
            }
        }
    }
    &__pill-remove {
        margin-left: 5px;
        padding: 5px;
        font-size: 12px;
    }
}
</style>